* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  min-height: 100vh;
}

.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.header {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.header h1 {
  color: #fff;
  font-size: 1.5rem;
}

.header-actions {
  display: flex;
  gap: 0.5rem;
}

.add-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 0.6rem 1.2rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  transition: transform 0.2s, box-shadow 0.2s;
}

.add-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);
}

.reset-btn {
  background: rgba(255, 255, 255, 0.1);
  color: #aaa;
  border: 1px solid rgba(255, 255, 255, 0.2);
  padding: 0.6rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 500;
  transition: all 0.2s;
}

.reset-btn:hover {
  background: rgba(255, 255, 255, 0.15);
  color: #fff;
}

.add-form {
  background: rgba(255, 255, 255, 0.05);
  padding: 1rem 2rem;
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.add-form input {
  flex: 1;
  min-width: 200px;
  padding: 0.6rem 1rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 6px;
  background: rgba(255, 255, 255, 0.05);
  color: #fff;
  font-size: 0.9rem;
}

.add-form input::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.form-actions {
  display: flex;
  gap: 0.5rem;
}

.add-form button {
  padding: 0.6rem 1rem;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
  border: none;
  transition: all 0.2s;
}

.add-form button:first-child {
  background: #667eea;
  color: white;
}

.add-form button:last-child {
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
}

.board {
  flex: 1;
  display: flex;
  gap: 1.5rem;
  padding: 2rem;
  overflow-x: auto;
}

.column {
  flex: 1;
  min-width: 300px;
  max-width: 400px;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 12px;
  padding: 1rem;
  border: 1px solid rgba(255, 255, 255, 0.05);
}

.column-title {
  color: #fff;
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.task-count {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.6);
  padding: 0.2rem 0.6rem;
  border-radius: 12px;
  font-size: 0.75rem;
}

.tasks {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  min-height: 200px;
}

.task {
  background: rgba(255, 255, 255, 0.08);
  border-radius: 10px;
  padding: 1rem;
  cursor: grab;
  transition: all 0.2s;
  border-left: 4px solid transparent;
}

.task:hover {
  background: rgba(255, 255, 255, 0.12);
  transform: translateX(4px);
}

.task:active {
  cursor: grabbing;
}

/* Status-based task styling */
.task.status-pending {
  border-left-color: #f59e0b;
}

.task.status-active {
  border-left-color: #3b82f6;
}

.task.status-completed {
  border-left-color: #10b981;
}

.task.status-blocked {
  border-left-color: #ef4444;
  background: rgba(239, 68, 68, 0.1);
}

.task-header {
  display: flex;
  align-items: flex-start;
  gap: 0.5rem;
}

.status-badge {
  font-size: 0.9rem;
  flex-shrink: 0;
}

.task-header h3 {
  color: #fff;
  font-size: 0.95rem;
  font-weight: 500;
  flex: 1;
  word-break: break-word;
}

.task-header-buttons {
  display: flex;
  gap: 0.25rem;
}

.edit-btn,
.delete-btn {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.3);
  font-size: 1rem;
  cursor: pointer;
  padding: 0.2rem;
  line-height: 1;
  transition: all 0.2s;
  border-radius: 4px;
}

.edit-btn:hover {
  color: #60a5fa;
  background: rgba(96, 165, 250, 0.1);
}

.delete-btn:hover {
  color: #ef4444;
  background: rgba(239, 68, 68, 0.1);
}

/* Modal styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  backdrop-filter: blur(4px);
}

.modal {
  background: linear-gradient(135deg, #1e293b 0%, #0f172a 100%);
  border-radius: 16px;
  padding: 2rem;
  min-width: 400px;
  max-width: 500px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.5);
}

.modal h3 {
  color: #fff;
  margin-bottom: 1.5rem;
  font-size: 1.25rem;
}

.modal input,
.modal textarea {
  width: 100%;
  padding: 0.75rem 1rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.05);
  color: #fff;
  font-size: 0.95rem;
  margin-bottom: 1rem;
  font-family: inherit;
}

.modal input:focus,
.modal textarea:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.2);
}

.modal input::placeholder,
.modal textarea::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.modal textarea {
  resize: vertical;
  min-height: 80px;
}

.modal-actions {
  display: flex;
  gap: 0.75rem;
  margin-top: 0.5rem;
}

.modal-actions button {
  flex: 1;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.2s;
  border: none;
}

.save-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.save-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);
}

.cancel-btn {
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
}

.cancel-btn:hover {
  background: rgba(255, 255, 255, 0.15);
}

.task-desc {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.85rem;
  margin-top: 0.5rem;
  line-height: 1.4;
}

.blocked-reason {
  background: rgba(239, 68, 68, 0.2);
  color: #fca5a5;
  padding: 0.5rem;
  border-radius: 6px;
  font-size: 0.8rem;
  margin-top: 0.5rem;
}

.task-meta {
  display: flex;
  gap: 0.75rem;
  margin-top: 0.75rem;
  flex-wrap: wrap;
}

.assignee {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.75rem;
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.timestamp {
  color: rgba(255, 255, 255, 0.3);
  font-size: 0.7rem;
}

.task-actions {
  margin-top: 0.75rem;
  padding-top: 0.75rem;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

.block-btn {
  background: rgba(239, 68, 68, 0.2);
  color: #fca5a5;
  border: none;
  padding: 0.4rem 0.8rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.75rem;
  transition: all 0.2s;
}

.block-btn:hover {
  background: rgba(239, 68, 68, 0.3);
}

.block-btn.unblock {
  background: rgba(16, 185, 129, 0.2);
  color: #6ee7b7;
}

.block-btn.unblock:hover {
  background: rgba(16, 185, 129, 0.3);
}

.block-form {
  margin-top: 0.75rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.block-form input {
  padding: 0.5rem;
  border: 1px solid rgba(239, 68, 68, 0.3);
  border-radius: 6px;
  background: rgba(239, 68, 68, 0.1);
  color: #fff;
  font-size: 0.85rem;
}

.block-form input::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.block-form-actions {
  display: flex;
  gap: 0.5rem;
}

.block-form button {
  flex: 1;
  padding: 0.4rem;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.8rem;
  transition: all 0.2s;
}

.block-form button:first-child {
  background: #ef4444;
  color: white;
}

.block-form button:last-child {
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
}

.footer {
  background: rgba(255, 255, 255, 0.02);
  padding: 1rem 2rem;
  text-align: center;
  color: rgba(255, 255, 255, 0.3);
  font-size: 0.8rem;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

/* Drag states */
.column:has(.task:active) {
  background: rgba(102, 126, 234, 0.1);
}

/* Loading and status */
.loading-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.loader {
  color: #fff;
  font-size: 1.5rem;
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

.saving-indicator {
  color: #fbbf24;
  font-size: 0.85rem;
  animation: pulse 1s infinite;
}

.error-indicator {
  color: #f87171;
  font-size: 0.85rem;
  cursor: help;
}

.error-banner {
  background: rgba(239, 68, 68, 0.2);
  color: #fca5a5;
  padding: 0.75rem 2rem;
  text-align: center;
  font-size: 0.85rem;
  border-bottom: 1px solid rgba(239, 68, 68, 0.3);
}

/* Priority badges */
.priority-badge {
  font-size: 0.9rem;
  flex-shrink: 0;
  cursor: pointer;
  padding: 0.2rem;
  border-radius: 4px;
  transition: all 0.2s;
}

.priority-badge:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: scale(1.1);
}

.priority-high {
  border-left-color: #ef4444 !important;
}

.priority-medium {
  border-left-color: #f59e0b !important;
}

.priority-low {
  border-left-color: #10b981 !important;
}

/* Priority dropdown */
.priority-dropdown {
  position: absolute;
  z-index: 100;
  background: linear-gradient(135deg, #1e293b 0%, #0f172a 100%);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  margin-top: 0.5rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
  min-width: 120px;
}

.priority-dropdown button {
  background: rgba(255, 255, 255, 0.05);
  color: #fff;
  border: none;
  padding: 0.5rem 0.75rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.85rem;
  text-align: left;
  transition: all 0.2s;
  white-space: nowrap;
}

.priority-dropdown button:hover {
  background: rgba(255, 255, 255, 0.1);
  transform: translateX(4px);
}

.priority-dropdown button:last-child {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  margin-top: 0.25rem;
  padding-top: 0.5rem;
}

/* Move buttons */
.move-buttons {
  display: flex;
  gap: 0.5rem;
  margin-top: 0.75rem;
  padding-top: 0.75rem;
  border-top: 1px solid rgba(255, 255, 255, 0.05);
}

.move-btn {
  flex: 1;
  background: rgba(59, 130, 246, 0.2);
  color: #93c5fd;
  border: none;
  padding: 0.5rem 0.75rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.75rem;
  transition: all 0.2s;
  font-weight: 500;
  min-height: 44px; /* Touch-friendly */
}

.move-btn:hover {
  background: rgba(59, 130, 246, 0.3);
  transform: translateY(-2px);
}

.move-btn:active {
  transform: translateY(0);
}

/* Status button */
.status-btn {
  background: rgba(168, 85, 247, 0.2);
  color: #c4b5fd;
  border: none;
  padding: 0.4rem 0.8rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.75rem;
  transition: all 0.2s;
  margin-right: 0.5rem;
}

.status-btn:hover {
  background: rgba(168, 85, 247, 0.3);
}

/* Agent info */
.agent-info {
  display: flex;
  gap: 0.5rem;
  margin-top: 0.5rem;
  flex-wrap: wrap;
}

.agent-label,
.session-label {
  background: rgba(59, 130, 246, 0.2);
  color: #93c5fd;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-size: 0.7rem;
  display: inline-flex;
  align-items: center;
  gap: 0.25rem;
}

.session-label {
  background: rgba(16, 185, 129, 0.2);
  color: #6ee7b7;
}

/* Status modal */
.status-modal {
  max-width: 600px;
}

.status-details {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.status-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 6px;
}

.status-row strong {
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.85rem;
  font-weight: 600;
}

.status-row span {
  color: #fff;
  font-size: 0.9rem;
  text-align: right;
}

.blocked-highlight {
  background: rgba(239, 68, 68, 0.15);
  border: 1px solid rgba(239, 68, 68, 0.3);
}

.status-note {
  background: rgba(59, 130, 246, 0.1);
  color: #93c5fd;
  padding: 0.75rem;
  border-radius: 6px;
  font-size: 0.85rem;
  margin-top: 0.5rem;
  border: 1px solid rgba(59, 130, 246, 0.2);
}

.session-link {
  color: #6ee7b7 !important;
  cursor: help;
  font-family: 'Monaco', 'Courier New', monospace;
  font-size: 0.8rem !important;
}

/* Task relative positioning for dropdowns */
.task {
  position: relative;
}

/* Responsive */
@media (max-width: 768px) {
  .header {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }
  
  .board {
    padding: 1rem;
  }
  
  .column {
    min-width: 280px;
  }
  
  .add-form {
    flex-direction: column;
  }
  
  .add-form input {
    min-width: 100%;
  }
  
  .modal {
    min-width: 90%;
    max-width: 90%;
  }
  
  .status-row {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.25rem;
  }
  
  .status-row span {
    text-align: left;
  }
  
  .move-buttons {
    flex-direction: column;
  }
  
  .move-btn {
    width: 100%;
  }
}
